.banner{
    background-image: url('https://github.com/hexschool/webLayoutTraining1st/blob/master/exhibition-week6/ricardo-gomez-angel-UrOZTfXT0h0-unsplash%20(1).jpg?raw=true');
    max-width: 100%;
    height: 480px;
    background-position: center center;
    background-size: cover;
    button{
        width: 160px;
    }
}
.menu{
    border-top: 1px solid #707070;
    border-bottom: 1px solid #707070;
    li{
        padding: 14px 18px;
        margin-bottom: 0;
        a{
            color: white;
        }
    }
    li:hover{
        border-bottom: 1px solid #000;
    }
}
.title{
    margin-bottom: 24px;
    padding-top: 60px;
}
.card-body{
    height: 270px;
}
.card-img{
    height: 350px;
}
.card{
  margin-bottom: 20px;
}
.card-text{
    margin-bottom: 34px;
}
.bg-live{
    background-image: url('https://github.com/hexschool/webLayoutTraining1st/blob/master/exhibition-week6/people2.png?raw=true');
    max-width: 100%;
    height: 482px;
    background-position: center center;
    background-size: cover;
    img{
        width: 128px;
        height: 128px;
    }
}
.bg-content-live{
    background-color: #190014CC;
    width: 100%;
    height: 482px;
    padding: 40px 24px;
}
.subscribe{
    background-image: url('https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/exhibition-week6/wladislaw-peljuchno-Y_f1aHpR1tc-unsplash.jpg');
    max-width: 100%;
    height: 400px;
    background-position: center center;
    background-size: cover;
}
.guess{
    img{
        width: 350px;
        height: 350px;
    }
}
.bg-cover{
    background-size: cover;
    background-position: center center;
}

.design-img{
    width: 445px;
    height: 285px;
}
.cart-img{
    width: 120px;
    height: 120px;
}
.design-card{
    padding-top: 80px;
    padding-bottom: 52px;
}
.art-img{
    width: 350px;
    height: 559px;
}
.show-img{
    width: 540px;
    height: 256px;
}
.art-inputgroup{
    width: 255px;
    height: 256px;
}
.art-button{
    width: 255px;
    height: 44px;
}
.border-left{
    border-left: 1px solid #000;
}

@media (max-width: 768px){
    .show-img{
        width: 345px;
        height: 400px;
    }
    .art-img{
        width: 345px;
        height: 559px;
    }
}
